.swiper-container {
  width: 50%;
  height: 50%;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  border-radius: 15px;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: rgb(192,192,192);
  /* Center slide text vertically */
  -webkit-flex-direction: column;
          flex-direction: column;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  /* align-items: center;  */
  
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


.button--flex{
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
}

.button--small {
  padding: 0.75rem 1rem;
}

.button--link {
  padding: 0;
  background-color: transparent;
  color:  hsl(230,69%, 61%);
}

.button--link:hover {
  /* background-color: transparent; */
  color: #FFF;
  -webkit-align-items: center;
          align-items: center;
}


.services__button {
  cursor: pointer;
  font-size: 1rem;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-decoration: none;
  /* display: block; */
  text-align: center;


  display: inline-block;
    background-color: hsl(230,57%, 53%);
    color: #FFF;
    padding: 1rem;
    border-radius: 0.5rem;
    font-weight: 500;
}

.services__button:hover .button__icon{
  -webkit-transform: translateX(0.25rem);
          transform: translateX(0.25rem)
}
