.skills__container{
    grid-row-gap: 0;
    row-gap: 0;
}

.skills__header{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 1rem;
    cursor: pointer;
}


.skills__icon,
.skills__arrow {
    font-size: 2rem;
    color:  hsl(230,69%, 61%);
}


.skills__icon{
margin-right: 0.75rem;
}

.skills__title{
    font-size: 1.25rem;
}

.skills__subtitle{
    font-size: 0.875rem;
    color:hsl(230,8%, 65%);
}

.skills__list{
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
    padding-left: 2.7rem;
}

.skills__arrow{
    margin-left: auto;
    cursor: pointer;
    transition: 0.4s;
    
}

.skills__titles{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 0.5rem;
}

.skills__name{
    font-size: 1rem ;
    font-weight:  500;
}

.skills__bar,
.skills__percentage{
    height: 5px;
    border-radius: 0.25rem;
}


.skills__bar{
    background-color: hsl(230,92%, 85%);
}


.skills__percentage{
    display: block;
    background-color:  hsl(230,69%, 61%);
}


.skills__html{
    width: 90% ;
}

.skills__css{
    width: 80% ;
}

.skills__react{
    width: 80% ;
}

.skills__reactnative{
    width: 80% ;
}

.skills__javascript{
    width: 50% ;
}


.skills__cpp{
    width: 80% ;
}

.skills__java{
    width: 70% ;
}

.skills__python{
    width: 60% ;
}

.skills__close{
    height:0;
    overflow: hidden;
}

.skills__open{
    height: -webkit-max-content;
    height: max-content;
    margin-bottom: 2.5rem;
}

.skills__openarrow{
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}

.skills__content {
    display:grid;
    height: -webkit-max-content;
    height: max-content;
    margin-bottom: 2rem;
}


.skills__button{
    font-size: 1.25rem;
    font-weight:500;
    cursor: pointer;
}


.about__tabs{
    display: -webkit-flex;
    display: flex;
    /* justify-content: space-evenly; */

    margin-bottom:2rem;

    /* flex-direction: row; */
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;

    width: 35%;
    padding: 8px 24px;
}

.about__button{
    font-size: 1.25rem;
    font-weight:500;
    cursor: pointer;
    
}

.about__button:hover{
    color: hsl(230,69%, 61%);
}

.about__icon{
    font-size: 1.8rem;
    
    margin-right: 0.25rem;
}

.about__data{
    display: grid;
    grid-template-columns: 0fr -webkit-max-content 1fr;
    grid-template-columns: 0fr max-content 1fr;
    grid-column-gap: 1.5rem;
    -webkit-column-gap: 1.5rem;
            column-gap: 1.5rem;
}

.about__title {
    font-size: 1rem ;
    font-weight: 500;
}

.about__subtitle {
    display: inline-block;
    font-size: 0.875rem;
    margin-bottom: 0rem;
}

.about__calendar {
    font-size: 0.813rem;
    color: hsl(230,8%, 65%);
    margin-bottom: 1rem;
}

.about__rounder {
    display: inline-block;
    width: 13px;
    height: 13px;
    background-color: hsl(230,69%, 61%);
    border-radius: 50%;
}

.about__line{
    display: block;
    width: 1px;
    height: 100%;
    background-color: hsl(230,69%, 61%);
    -webkit-transform: translate(6px, -7px);
            transform: translate(6px, -7px);
}

.about [data-content] {
    display: none;
}

.about__active[data-content]{
    display: block;
}

.about__button.about__active {
    color:hsl(230,69%, 61%);
}

@media only screen and (min-width: 260px) and (max-width: 480px) {

    .about__tabs{
        -webkit-align-items: flex-start;
                align-items: flex-start;
        -webkit-justify-content: space-around;
                justify-content: space-around;
            width: 95%;
            padding: 0px 0px 0px 0px;
            


    }



    .about__title {
        font-size: 1.2rem ;
        font-weight: 500;
    }
    
    .about__subtitle {
        display: inline-block;
        font-size: 1rem;
        margin-bottom: 0rem;
    }

    .about__icon{
        font-size: 1.8rem;
        
        margin-right: 0.25rem;
    }


}

@media only screen and (min-width: 760px) and (max-width: 1000px) {
    
    .about__tabs{
        -webkit-align-items: flex-start;
                align-items: flex-start;
        -webkit-justify-content: space-around;
                justify-content: space-around;
            width: 75%;
            padding: 0px 0px 0px 0px;
            


    }
}



@media only screen and (min-width: 1024px) and (max-width: 1366px) {
    
    .about__tabs{
        -webkit-align-items: flex-start;
                align-items: flex-start;
        -webkit-justify-content: space-around;
                justify-content: space-around;
            width: 65%;
            padding: 0px 0px 0px 0px;
            


    }
}


